/*Общие стили*/

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 300px;
}

.wrapper {
  max-width: 1070px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.wrap {
  margin-left: auto;
  margin-right: auto;
  font-size: 0;
  max-width: 800px;
}

.ears::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.d-none {
  display: none;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

input,
button,
textarea {
  outline: none;
}

/*--Название Проекта--*/

@media screen and (max-width: 920px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 700px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}